import uniPopup from '@/components/uni-popup/uni-popup.vue';
let that;
export default {
	data() {
		return {
			isok: true,
			isSubmit: false,
			isShenhe: false,
			isMask: false,
			isAuth: false,
			timeShow: true,
			count: '',
			timer: null,
			userName: '',
			phone: '',
			authCode: '',
			isRegist: false, //是否已经注册
			userCode: '',
			userInfo: '',
			islogin: false,
			encryptedData: '',
			encryptedSecret: '',
			iv: ''
		};
	},
	onLoad() {
		that = this;
		that.isShowAuthPop();
		that.loginForCode();
	},
	onShow() {
		// this.toCheckIngPage()
	},
	components: {
		uniPopup
	},
	methods: {
		openPop() {
			this.$refs.authPop.open();
		},
		// 登录获取code 每次请求需要重新获取code
		// 账户审核中即跳转审核海报页
		toCheckIngPage() {
			if (!this.isRegist && !this.isShenhe) {
				uni.navigateTo({
					url: '../auditAccount/auditAccount'
				});
			}
		},
		loginForCode() {
			uni.login({
				success(e) {
					console.log(e);
					that.userCode = e.code;
					console.log(that.userCode);
					uni.getUserInfo({
						success: res => {
							console.log(res);
							that.userInfo = res.userInfo;
							that.encryptedData = res.encryptedData;
							that.iv = res.iv;
							that.loginAuth();
						}
					});
				}
			});
		},
		//  直接获取code
		getLoginCode() {
			uni.login({
				success(e) {
					that.userCode = e.code;
					console.log(that.userCode);
					uni.getUserInfo({
						success: res => {
							console.log(res);
							that.userInfo = res.userInfo;
							that.encryptedData = res.encryptedData;
							that.iv = res.iv;
						}
					});
				}
			});
		},
		//  注册提交
		signInSubmit() {
			let oo = that.checkName();
			let u = that.userInfo;
			// this.getLoginCode()
			uni.login({
				success(e) {
					console.log(e);
					that.userCode = e.code;
					console.log(that.userCode);
					uni.getUserInfo({
						success: res => {
							console.log(res);
							that.userInfo = res.userInfo;
							that.encryptedData = res.encryptedData;
							that.iv = res.iv;
							if (oo) {
								that.$http.request({
									url: '/api/auth/wechatServicerUserRegister',
									data: {
										code: that.userCode,
										realName: that.userName,
										phoneNumber: that.phone,
										verificationCode: that.authCode,
										encryptedData: that.encryptedData,
										encryptedSecret: that.iv
									},
									success: e => {
										console.log(e);
										if (e.data.code == 100000) {
											that.isRegist = true;
											uni.showToast({
												title: '注册成功',
												success() {}
											});
											// 跳转至 提交完成海报审核页面
											uni.navigateTo({
												url: '../auditAccount/auditAccount'
											});
										} else if ((e.data.code = 100304)) {
											uni.showToast({
												icon: 'none',
												title: '验证码错误'
											});
										} else if ((e.data.code = 100308)) {
											uni.showToast({
												icon: 'none',
												title: '请求太频繁，请稍后重试'
											});
										}
									}
								});
							}
						}
					});
				}
			});
		},
		//  查询店铺审核状态
		queryShopAuditStatus() {},
		//  登录提交
		loginSubmit() {
			//  跳转首页失灵 可能是同时打开页面过多

			//校验手机号
			let cc = this.$api.checkPhone(that.phone);
			let u = that.userInfo;

			// this.getLoginCode()
			if (!cc) {
				uni.showToast({
					icon: 'none',
					title: '手机号不正确'
				});
				return;
			} else if (!that.authCode) {
				uni.showToast({
					icon: 'none',
					title: '验证码为空'
				});
				return;
			} else if (that.authCode) {
				uni.login({
					success(e) {
						that.userCode = e.code;
						console.log(that.userCode);
						uni.getUserInfo({
							success: res => {
								console.log(res);
								that.userInfo = res.userInfo;
								that.encryptedData = res.encryptedData;
								that.iv = res.iv;
								that.$http.request({
									url: '/api/auth/wechatServicerUserLogin',
									data: {
										code: that.userCode,
										phoneNumber: that.phone,
										verificationCode: that.authCode,
										encryptedData: that.encryptedData,
										encryptedSecret: that.iv
									},
									success: e => {
										console.log('登录', e.data);
										if (e.data.code == 100000) {
											//  本地缓存用户信息
											uni.setStorageSync('user', e.data.servicerUserInfo);
											uni.setStorageSync('userId', e.data.servicerUserInfo.servicerUserId);
											uni.showToast({
												title: '登录成功'
											});
											// 查询用户的审核状态 进行判断 ，没有门店即跳转添加门店页面

											//  服务者未审核
											if (e.data.servicerUserInfo.servicerState == 1) {
												uni.navigateTo({
													url: '../auditAccount/auditAccount'
												});
												//  通过审核 即可添加门店
											} else if (e.data.servicerUserInfo.servicerState == 2) {
												uni.navigateTo({
													url: '../shop/shopAddIndex/shopAddIndex'
												});
												//  审核未通过
											} else if (e.data.servicerUserInfo.servicerState == 3) {
												uni.navigateTo({
													url: '../shop/storeAudit/storeAudit'
												});
											}
										} else if ((e.data.code = 100304)) {
											uni.showToast({
												icon: 'none',
												title: '验证码错误'
											});
										} else if ((e.data.code = 100308)) {
											uni.showToast({
												icon: 'none',
												title: '请求太频繁，请稍后重试'
											});
										} else {
											uni.showToast({
												title: e.msg
											});
										}
									}
								});
							}
						});
					}
				});
			}
		},
		//校验验证码 (无法校验，后端验证)
		//校验中文姓名
		checkName() {
			let ok = this.$api.checkChinaName(that.userName);
			let cc = this.$api.checkPhone(that.phone);
			if (!ok) {
				uni.showToast({
					title: '姓名输入有误',
					icon: 'none'
				});
				return;
			} else if (!cc) {
				uni.showToast({
					title: '手机号不正确',
					icon: 'none'
				});
				return;
			}
			ok = ok && cc && that.authCode;
			return ok;
		},

		//登录授权   判断用户是否已经成为 服务者用户
		loginAuth() {
			that.$http
				.request({
					url: '/api/auth/wechatServicerUserAuthAndIsServicerUser',
					data: {
						code: that.userCode,
						encryptedData: that.encryptedData,
						encryptedSecret: that.iv
					}
				})
				.then(res => {
					// console.log(res)
					if (res.data.code == 100000) {
						// uni.showToast({
						// 	title: '授权成功'
						// });
						// console.log(res.data.isServicerUser);
						//  已经是服务者
						if (res.data.isServicerUser == 1) {
							that.isRegist = true;

							// 非服务者
						} else if (res.data.isServicerUser == 0) {
							that.isRegist = false;
						}
						that.isok = true;
						uni.hideLoading();
						// console.log(that.isRegist);
					}
				});
		},
		getAuthCode() {
			let tk = that.$api.checkPhone(that.phone);
			// console.log(tk, that.phone);
			if (tk && that.phone) {
				// 验证是否是手机号
				this.getCode();
				// 获取验证码
				that.$http.request({
					url: '/api/sendSms/sendVerificationCode',
					data: {
						phoneNumber: that.phone
					},
					success(e) {
						// console.log(e);
					}
				});
			}
		},
		isShowAuthPop() {
			uni.getUserInfo({
				success(e) {
					that.userInfo = e.userInfo;
					if (e.userInfo) {
						// that.isMask = false;
						that.isAuth = true;
					} else {
						// that.isMask = true;
						that.isAuth = false;
					}
				}
			});
		},
		takePhone() {
			uni.makePhoneCall({
				phoneNumber: 16368111120,
				success(e) {
					// console.log(e);
				},
				fail(e) {
					// console.log(e);
				}
			});
		},
		//  获取用户信息即授权
		sureAuth() {
			uni.getUserInfo({
				success: e => {
					console.log(e);
					if (e.userInfo) {
						that.isMask = false;
						that.isAuth = true;
					}
				}
			});
		},
		getCode() {
			const TIME_COUNT = 60;
			if (!this.timer) {
				this.count = TIME_COUNT;
				this.timeShow = false;
				this.timer = setInterval(() => {
					if (this.count > 0 && this.count <= TIME_COUNT) {
						this.count--;
					} else {
						this.show = true;
						clearInterval(this.timer);
						this.timer = null;
					}
				}, 1000);
			}
		}
	}
};
